<template>
  <section
    class="ml-character"
    ref="character"
  >
    <p class="ml-character__title">
      Аватар
    </p>
    <div class="ml-character__avatar">

      <div
        class="ml-character__avatar-equipment-modal"
        :class="{
          'ml-character__avatar-equipment-modal--visible': itemModal.visible,
        }"
        :style="itemModalStyle"
        ref="itemModal"
      >
        <p class="ml-character__avatar-equipment-modal-info">
          {{ itemModalContent.name }} [{{ itemModalContent.itemLevel }}]
        </p>
        <ml-characteristic-table
          v-if="Object.values(itemModalContent).length"
          highlight-updates
          :characteristic-update-list-map="{
            [AVATAR_SETTING_MAP.HEALTH]: itemModalContent.health.updated,
            [AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE]: itemModalContent.elementalDamage.updated,
            [AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE]: itemModalContent.elementalResistance.updated,
            [AVATAR_SETTING_MAP.BLOOD_DAMAGE]: itemModalContent.bloodDamage.updated,
            [AVATAR_SETTING_MAP.BLOOD_RESISTANCE]: itemModalContent.bloodResistance.updated,
            [AVATAR_SETTING_MAP.MENTAL_DAMAGE]: itemModalContent.mentalDamage.updated,
            [AVATAR_SETTING_MAP.MENTAL_RESISTANCE]: itemModalContent.mentalResistance.updated,
            [AVATAR_SETTING_MAP.CHAOS_DAMAGE]: itemModalContent.chaosDamage.updated,
            [AVATAR_SETTING_MAP.CHAOS_RESISTANCE]: itemModalContent.chaosResistance.updated,
          }"
          :characteristic-map="{
            [AVATAR_SETTING_MAP.HEALTH]: itemModalContent.health.value,
            [AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE]: itemModalContent.elementalDamage.value,
            [AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE]: itemModalContent.elementalResistance.value,
            [AVATAR_SETTING_MAP.BLOOD_DAMAGE]: itemModalContent.bloodDamage.value,
            [AVATAR_SETTING_MAP.BLOOD_RESISTANCE]: itemModalContent.bloodResistance.value,
            [AVATAR_SETTING_MAP.MENTAL_DAMAGE]: itemModalContent.mentalDamage.value,
            [AVATAR_SETTING_MAP.MENTAL_RESISTANCE]: itemModalContent.mentalResistance.value,
            [AVATAR_SETTING_MAP.CHAOS_DAMAGE]: itemModalContent.chaosDamage.value,
            [AVATAR_SETTING_MAP.CHAOS_RESISTANCE]: itemModalContent.chaosResistance.value,
          }"
        />
      </div>

      <!-- left character interface -->
      <div class="ml-character__avatar-side ml-character__avatar-side--left">
        <ml-clothes-slot
          class="ml-character__slot ml-character__slot--small ml-character__slot--amulet-1"
          :clothes-type="CLOTHES_TYPE.AMULET"
          :clothes-key="0"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
        <ml-clothes-slot
          class="ml-character__slot ml-character__slot--small ml-character__slot--amulet-2"
          :clothes-type="CLOTHES_TYPE.AMULET"
          :clothes-key="1"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
        <ml-clothes-slot
          class="ml-character__slot ml-character__slot--weapon"
          :clothes-type="CLOTHES_TYPE.WEAPON"
          :clothes-key="'left'"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
        <ml-clothes-slot
          class="ml-character__slot ml-character__slot--small ml-character__slot--ring-1"
          :clothes-type="CLOTHES_TYPE.RING"
          :clothes-key="0"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
        <ml-clothes-slot
          class="ml-character__slot ml-character__slot--small ml-character__slot--ring-2"
          :clothes-type="CLOTHES_TYPE.RING"
          :clothes-key="1"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
        <ml-clothes-slot
          class="ml-character__slot ml-character__slot--dress"
          :clothes-type="CLOTHES_TYPE.DRESS"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
      </div>
      <!-- end left character interface -->

      <!-- start center character interface -->
      <div class="ml-character__avatar-side ml-character__avatar-side--center">
        <ml-clothes-slot
          class="ml-character__slot"
          :clothes-type="CLOTHES_TYPE.HAT"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
        <div class="ml-character__slot ml-character__slot--filler">
          <img
            class="ml-character__slot-filler"
            src="@picture/avatar.jpg"
            alt="avatar"
          >
        </div>
        <ml-clothes-slot
          class="ml-character__slot"
          :clothes-type="CLOTHES_TYPE.SHOE"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
      </div>
      <!-- end center character interface -->

      <!-- start right character interface -->
      <div class="ml-character__avatar-side ml-character__avatar-side--right">
        <ml-clothes-slot
          class="ml-character__slot ml-character__slot--small ml-character__slot--amulet-1"
          :clothes-type="CLOTHES_TYPE.AMULET"
          :clothes-key="2"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
        <ml-clothes-slot
          class="ml-character__slot ml-character__slot--small ml-character__slot--amulet-2"
          :clothes-type="CLOTHES_TYPE.AMULET"
          :clothes-key="3"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
        <ml-clothes-slot
          class="ml-character__slot ml-character__slot--weapon"
          :clothes-type="CLOTHES_TYPE.WEAPON"
          :clothes-key="'right'"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
        <ml-clothes-slot
          class="ml-character__slot ml-character__slot--small ml-character__slot--ring-1"
          :clothes-type="CLOTHES_TYPE.RING"
          :clothes-key="2"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
        <ml-clothes-slot
          class="ml-character__slot ml-character__slot--small ml-character__slot--ring-2"
          :clothes-type="CLOTHES_TYPE.RING"
          :clothes-key="3"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
        <ml-clothes-slot
          class="ml-character__slot ml-character__slot--dress"
          :clothes-type="CLOTHES_TYPE.PANT"
          @on-mouse-move="updateItemModalPosition"
          @on-mouse-enter="onSlotMouseEnter"
          @on-mouse-leave="hideItemModal"
        />
      </div>
      <!-- end right character interface -->

    </div>
  </section>
</template>

<script>
// components
import MlClothesSlot from '@component/clothes-slot/clothes-slot';
import MlCharacteristicTable from '@component/characteristic-table/characteristic-table';

// js
import { AVATAR_SETTING_MAP } from '@script/constant/avatar-setting';
import { CLOTHES_TYPE } from '@script/constant/clothes-type';

export default {
  name: 'Ml-character',
  components: {
    MlClothesSlot,
    MlCharacteristicTable,
  },
  data: () => ({
    AVATAR_SETTING_MAP,
    CLOTHES_TYPE,
    itemModal: {
      visible: false,
      top: 0,
      left: 0,
      width: '',
      height: '',
    },
    itemModalContent: {},
  }),
  computed: {
    itemModalStyle () {
      return { transform: `translate(${this.itemModal.left}px, ${this.itemModal.top}px)`}
    },
  },
  methods: {
    updateItemModalPosition({top, left}) {
      this.itemModal.top = top - this.$refs.itemModal.offsetParent.offsetTop;
      this.itemModal.left = left - this.$refs.itemModal.offsetParent.offsetLeft;
    },
    onSlotMouseEnter(equipmentData) {
      this.itemModalContent = equipmentData;
      this.itemModal.visible = true;
    },
    hideItemModal() {
      this.itemModal.visible = false;
      this.itemModalContent = {};
    },
  },
};
</script>

<style lang="scss">
  @import './character.scss';
</style>
