<template>
  <section class="ml-avatar-setting">
    <p class="ml-avatar-setting__title">
      Меню персонажа
    </p>
    <div class="ml-avatar-setting__content">
      <div class="ml-avatar-setting__level">
        <p class="ml-avatar-setting__level-title">
          Уровень персонажа:
        </p>
        <label class="ml-avatar-setting__level-label">
          <select
            class="ml-avatar-setting__level-select"
            v-model="selectedLevel"
            @change="onChangeLevel"
          >
            <option
              v-for="level in LEVEL_OPTION_LIST"
              :value="level"
              :key="level"
            >
              Level: {{ level }}
            </option>
          </select>
        </label>
      </div>
      <div class="ml-avatar-setting__statistic">
        <p class="ml-avatar-setting__statistic-title">
          Характеристики персонажа
        </p>
        <ml-characteristic-table
          with-spacer
          show-empty
          :characteristic-map="{
            [AVATAR_SETTING_MAP.HEALTH]: health,
            [AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE]: elementalDamage,
            [AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE]: elementalResistance,
            [AVATAR_SETTING_MAP.BLOOD_DAMAGE]: bloodDamage,
            [AVATAR_SETTING_MAP.BLOOD_RESISTANCE]: bloodResistance,
            [AVATAR_SETTING_MAP.MENTAL_DAMAGE]: mentalDamage,
            [AVATAR_SETTING_MAP.MENTAL_RESISTANCE]: mentalResistance,
            [AVATAR_SETTING_MAP.CHAOS_DAMAGE]: chaosDamage,
            [AVATAR_SETTING_MAP.CHAOS_RESISTANCE]: chaosResistance,
          }"
        />
      </div>
    </div>
  </section>
</template>

<script>
import MlCharacteristicTable from '@component/characteristic-table/characteristic-table';
import { CLOTHES_TYPE } from '@script/constant/clothes-type';
import {
  AVATAR_SETTING_MAP,
  HEALTH,
  DEFAULT_LEVEL,
} from '@script/constant/avatar-setting';

const LEVEL_OPTION_LIST = new Array(24)
  .fill(0)
  .map((_, index) => index + 1);

export default {
  name: 'Ml-avatar-settings',
  components: {
    MlCharacteristicTable,
  },
  data: () => ({
    AVATAR_SETTING_MAP,
    LEVEL_OPTION_LIST,
    selectedLevel: DEFAULT_LEVEL,
  }),
  created() {
  },
  computed: {
    health() {
      const healthByLevel = HEALTH.START + this.selectedLevel * HEALTH.MULTIPLIER;
      return this.getValueByProperty(AVATAR_SETTING_MAP.HEALTH) + healthByLevel;
    },
    elementalDamage() {
      return this.getValueByProperty(AVATAR_SETTING_MAP.ELEMENTAL_DAMAGE);
    },
    bloodDamage() {
      return this.getValueByProperty(AVATAR_SETTING_MAP.BLOOD_DAMAGE);
    },
    chaosDamage() {
      return this.getValueByProperty(AVATAR_SETTING_MAP.CHAOS_DAMAGE);
    },
    mentalDamage() {
      return this.getValueByProperty(AVATAR_SETTING_MAP.MENTAL_DAMAGE);
    },
    elementalResistance() {
      return this.getValueByProperty(AVATAR_SETTING_MAP.ELEMENTAL_RESISTANCE);
    },
    bloodResistance() {
      return this.getValueByProperty(AVATAR_SETTING_MAP.BLOOD_RESISTANCE);
    },
    chaosResistance() {
      return this.getValueByProperty(AVATAR_SETTING_MAP.CHAOS_RESISTANCE);
    },
    mentalResistance() {
      return this.getValueByProperty(AVATAR_SETTING_MAP.MENTAL_RESISTANCE);
    },
  },
  methods: {
    getValueByProperty(property) {
      let count = 0;

      const amuletList = Object.values(this.$store.state.equipped[CLOTHES_TYPE.AMULET]);
      const weaponList = Object.values(this.$store.state.equipped[CLOTHES_TYPE.WEAPON]);
      const ringList = Object.values(this.$store.state.equipped[CLOTHES_TYPE.RING]);

      [
        ...amuletList,
        ...weaponList,
        ...ringList,
        this.$store.state.equipped[CLOTHES_TYPE.HAT],
        this.$store.state.equipped[CLOTHES_TYPE.SHOE],
        this.$store.state.equipped[CLOTHES_TYPE.DRESS],
        this.$store.state.equipped[CLOTHES_TYPE.PANT],
      ].forEach( (item) => {
        count += item[property].value;
      });

      return count;
    },
    onChangeLevel() {
      this.$store.dispatch('setLevel', this.selectedLevel);
    }
  },
};
</script>

<style lang="scss">
  @import './avatar-setting.scss';
</style>
