export const equipmentNormalizer = ({ data }) => {
  return {
    class: data.class,
    image: data.image,
    itemLevel: data.min_magic,
    name: data.name,
    health: {
      base: Number(data.add_max_hp ?? 0),
      value: Number(data.add_max_hp ?? 0),
      updated: false,
    },
    elementalDamage: {
      base: Number(data.add_element_damage ?? 0),
      value: Number(data.add_element_damage ?? 0),
      updated: false,
    },
    elementalResistance: {
      base: Number(data.add_element_defense ?? 0),
      value: Number(data.add_element_defense ?? 0),
      updated: false,
    },
    mentalDamage: {
      base: Number(data.add_mental_attack ?? 0),
      value: Number(data.add_mental_attack ?? 0),
      updated: false,
    },
    mentalResistance: {
      base: Number(data.add_mental_defense ?? 0),
      value: Number(data.add_mental_defense ?? 0),
      updated: false,
    },
    bloodDamage: {
      base: Number(data.add_blood_damage ?? 0),
      value: Number(data.add_blood_damage ?? 0),
      updated: false,
    },
    bloodResistance: {
      base: Number(data.add_blood_defense ?? 0),
      value: Number(data.add_blood_defense ?? 0),
      updated: false,
    },
    chaosDamage: {
      base: Number(data.add_chaos_damage ?? 0),
      value: Number(data.add_chaos_damage ?? 0),
      updated: false,
    },
    chaosResistance: {
      base: Number(data.add_chaos_defense ?? 0),
      value: Number(data.add_chaos_defense ?? 0),
      updated: false,
    },
  };
};

